<template>
	<div class="AreaPop">
		<div  class="AreaPopCon">
			<div class="title">
				<input type="text" v-model="areaInput" name="" placeholder="搜索国家/地区">
				<span @click.stop="closeArea"><i class="icon iconfont icon-cha"></i></span>
			</div>
			<div class="meun">
				<ul>
					<li v-for="(item,index) in areaCodeData" @click="chooseAreaCode(index)">
						<span >{{item.country}}</span>
						<span>{{item.areacode}}</span>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    spanClose:true,
			    areaInput:'',
			    areaData:{
			    	currAreaCode:'+86',
			    	currId:1
			    },
			    areaCodeData:[
			    	{
			    		city:'中国大陆',
			    		areaCode:'86'
			    	},
			    	{
			    		city:'香港',
			    		areaCode:'852'
			    	},
			    	{
			    		city:'澳门',
			    		areaCode:'853'
			    	},
			    	{
			    		city:'美国',
			    		areaCode:'1'
			    	},
			    	{
			    		city:'英国',
			    		areaCode:'44'
			    	},
			    	{
			    		city:'加拿大',
			    		areaCode:'1'
			    	},
			    	{
			    		city:'澳大利亚',
			    		areaCode:'61'
			    	},
			    	{
			    		city:'法国',
			    		areaCode:'33'
			    	}
			    ]

			}
		},
		// props:['titleCom','titleComTow','icon']
		props:{
			titleComTow:'',
			icon:{
				default:1
			}
		},
		 methods: {
		 	loadData:function(){
				var _this=this,json={};
				Lib.M.ajax({
			      	'url': memberAPI.activeAPI.findAreaPhone_POST,
			      	'type': 'POST',
			      	'data':json,
      				'success': function(data) {
      					_this.areaCodeData=data.data;
      					console.log('findAreaPhone',data)
      				}
      			})
				
			},
            closeArea(){
            	var _this=this;
            	_this.areaInput='';
		        _this.$emit('close-areaCode',_this.spanClose);
		    },
		    chooseAreaCode(ind){
		    	var _this=this;
		    	_this.areaData.currAreaCode=_this.areaCodeData[ind].areacode;
		    	_this.areaData.currId=_this.areaCodeData[ind].id;
		    	_this.$emit('curr-areaCode',_this.areaData);
            	console.log(ind)
		    }
        },
        created:function(){
	    	var _this=this;
	    	_this.loadData();
	    },
	}
</script>
<style scope>
	
</style>


